import React from 'react'
import { CustomButton } from '../../../globalComponents'

const InvestmentNotFound = (props) => {
    const {
        btnText,
        paragraphText,
        headingText,
        handleChange,
        type
    }=props
    return (
        <div className='investment-empty'>
            <img
                src={"../../images/investmentsNotFound.svg"}
                width={100}
                className="me-2"
            />{" "}
            <div>
                <h5>{headingText}</h5>
                <p>{paragraphText}</p>
                <CustomButton
                    text={btnText ||"Submit"}
                    classNames={" btn-primary d-inline"}
                    handleClick={()=>handleChange(type)}
                />
            </div>
        </div>
    )
}

export default InvestmentNotFound
