import React, { useEffect, useState } from 'react'
import { CustomTabs } from '../../../reuseableComponents'
import EmpOverview from '../EmpOverview';
import SalaryDetails from '../SalaryDetails';
import InvestmentNotFound from './InvestmentNotFound';
import { useDispatch, useSelector } from 'react-redux';
import BackIcon from '../../../commanComponent/BackIcon';
import { Link, useNavigate } from 'react-router-dom';
import { CustCheckbox, CustomStrong, Icon, Input, Label } from '../../../commanComponent';
import { financialYearActions, payrollActions } from '../../../../core/resource/actions';
import { filterCurrentFinancialYear, getInputPrefix, toTitleCase } from '../../../../utils/common';
import { useCustomTranslation } from '../../../../translation';
import { CustomButton } from '../../../globalComponents';
import CustomSelect from '../../../commanComponent/CustomSelect';

const Investments = () => {
    const { t } = useCustomTranslation()
    const {
        selectedEmployeeData,

        getEmployeeInvestmentPending,
        getEmployeeInvestmentRes,
        getEmployeeInvestmentError,

        employeeInvestmentProofRes,
        employeeInvestmentProofError,
        employeeInvestmentProofPending,

        getEmployeeInvestmentProofRes,
        getEmployeeInvestmentProofError,
        getEmployeeInvestmentProofPending,

        getAllEmployeeSalaryPending,
        getAllEmployeeSalaryRes,
        getAllEmployeeSalaryError,

    } = useSelector(state => state.payroll)

    const {
        getFinancialYearListRes,
        getFinancialYearListError,
        getFinancialYearListPending
    } = useSelector(state => state.financialYear)
    const [investmentType, setInvestmentType] = useState("itDeclaration")
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [itDeclarationData, setItDeclarationData] = useState('')
    const [proofOfInvestmentData, setProofOfInvestmentData] = useState('')
    const [financialYearListData, setFinancialYearListData] = useState('')
    const [selectedFinancialYear, setSelectedFinancialYear] = useState('')
    const [getAllEmployeeSalary, setAllEmployeeSalary] = useState([])
    const tabsRequest = [
        { name: "Overview", id: "overview" },
        { name: "Salary Details", id: "salaryDetails" },
        { name: "Investments", id: "investments" },
    ];

    const handleInvestmentTabChange = (type) => {
        setInvestmentType(type)
        if (type === "itDeclaration") {
            navigate("/payroll-employees/employee-detail/it-declaration")
        }
        else if (type === "proofOfInvestment") {
            navigate("/payroll-employees/employee-detail/proof-of-Investment")
        }
    }

    useEffect(() => {
        getEmployeeItDeclaration()
        getFinancialYearListData()
        getEmployeeInvestmentProofData()
        getEmployeeSalary()
    }, [])

    const getEmployeeItDeclaration = () => {
        const payload = {
            _id: selectedEmployeeData?._id
        }
        dispatch(payrollActions.getEmployeeInvestmentRequest(payload))
    }

    const getEmployeeSalary = ()=>{
        const payload = {
            _id:selectedEmployeeData?._id
        }
        dispatch(payrollActions.getAllEmployeeSalaryRequest(payload))
    }

    useEffect(() => {
        if (!getAllEmployeeSalaryPending) {
            if (getAllEmployeeSalaryError) {
                return;
            }
            if (getAllEmployeeSalaryRes) {
                if (getAllEmployeeSalaryRes?.code === 200) {
                    setAllEmployeeSalary(getAllEmployeeSalaryRes?.data)
                }
            }
        }
    }, [
        getAllEmployeeSalaryPending,
        getAllEmployeeSalaryRes,
        getAllEmployeeSalaryError,
    ]);

    useEffect(() => {
        if (!getEmployeeInvestmentPending) {
            if (getEmployeeInvestmentError) {
                return;
            }
            if (getEmployeeInvestmentRes) {
                if (getEmployeeInvestmentRes?.code === 200) {
                    setItDeclarationData(getEmployeeInvestmentRes?.data?.[0])
                    setProofOfInvestmentData(getEmployeeInvestmentRes?.data?.[0])
                }
            }
        }
    }, [
        getEmployeeInvestmentRes,
        getEmployeeInvestmentError,
        getEmployeeInvestmentPending
    ]);

    const renderedInvestmentData = () => {
        return (itDeclarationData?.section_6a_declarations?.map((item, index) => (
            <React.Fragment key={index}>
                <tr className="fw-bold">
                    <td colSpan="2">{item?.category}</td>
                </tr>
                {item?.sub_category?.map((subCatItem) => (

                    <tr key={subCatItem?._id}>
                        <td className="ps-4">{subCatItem?.sub_category_name}</td>
                        <td>{subCatItem?.input_value || "-"}</td>
                    </tr>
                ))}
                <tr>
                    <td colSpan="2">
                        <hr />
                    </td>
                </tr>
            </React.Fragment>
        )))

    }


    const handleFileChange = (e, index) => {
        const file = e.target.files[0];
        if (file) {
            const updatedData = [...proofOfInvestmentData?.section_6a_declarations];
            updatedData[index] = { ...updatedData[index], proof_of_investment: file };
            setProofOfInvestmentData({ ...proofOfInvestmentData, ["section_6a_declarations"]: updatedData });
            handleAddEmpInvestmentProof(file, updatedData[index])
        }

    };

    const handleAddEmpInvestmentProof = (file, item) => {
        const formData = new FormData()
        formData.append("employee_id", selectedEmployeeData?._id)
        formData.append("tax_regime", proofOfInvestmentData?.tax_regime)
        formData.append("financial_year", selectedFinancialYear?.value,)
        formData.append("investment_category_id", item?._id)
        formData.append("file", file)

        dispatch(payrollActions.employeeInvestmentProofRequest(formData))
    }

    useEffect(() => {
        if (!employeeInvestmentProofPending) {
            if (employeeInvestmentProofError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_EMPLOYEE_INVESTMENT_PROOF_STORE"))
                return;
            }
            if (employeeInvestmentProofRes) {
                if (employeeInvestmentProofRes?.code === 200) {
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_EMPLOYEE_INVESTMENT_PROOF_STORE"))
                }
            }
        }
    }, [
        employeeInvestmentProofRes,
        employeeInvestmentProofError,
        employeeInvestmentProofPending
    ]);

    const getEmployeeInvestmentProofData =()=>{
        dispatch(payrollActions.getEmployeeInvestmentProofRequest())

    }


    
    useEffect(() => {
        if (!getEmployeeInvestmentProofPending) {
            if (getEmployeeInvestmentProofError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_EMPLOYEE_INVESTMENT_PROOF_STORE"))
                return;
            }
            if (getEmployeeInvestmentProofRes) {
                if (getEmployeeInvestmentProofRes?.code === 200) {
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_EMPLOYEE_INVESTMENT_PROOF_STORE"))
                }
            }
        }
    }, [
        getEmployeeInvestmentProofRes,
        getEmployeeInvestmentProofError,
        getEmployeeInvestmentProofPending
    ]);

    /* List of Financial Year */
    const getFinancialYearListData = () => {
        const payload ={
            employee_id:selectedEmployeeData?._id
        }
        dispatch(financialYearActions.getFinancialYearListRequest(payload))
    }
    useEffect(() => {
        if (!getFinancialYearListPending) {
            if (getFinancialYearListError) {
                return;
            }
            if (getFinancialYearListRes) {
                if (getFinancialYearListRes?.code === 200) {
                    const currentFY = filterCurrentFinancialYear(getFinancialYearListRes?.data)
                    setSelectedFinancialYear(currentFY)
                    setFinancialYearListData(getFinancialYearListRes?.data)

                }
            }
        }
    }, [
        getFinancialYearListRes,
        getFinancialYearListError,
        getFinancialYearListPending
    ]);
    const renderedProfOfInvestment = () => {
        return (proofOfInvestmentData?.section_6a_declarations?.map((item, index) => (
            <React.Fragment key={index}>
                <tr className="fw-bold">
                    <td colSpan="2">{item?.category}</td>
                </tr>
                {item?.sub_category?.map((subCatItem) => (

                    subCatItem?.input_type === "number" && <tr key={subCatItem?._id}>
                        <td className="ps-4">{subCatItem?.sub_category_name}</td>
                        <td>{subCatItem?.input_value || "-"}</td>
                        {/* <td>{<Icon className="fa-light fa-message"/>}</td> */}
                        <td style={{ width: "250px" }}>
                            <div className="d-flex align-items-center gap-2">
                                <input
                                    type="file"
                                    id={`policyFile-${index}`}
                                    name="poster"
                                    className="d-none"
                                    onChange={(e) => handleFileChange(e, index)}
                                    accept=".png, .jpeg, .doc, .docx, .pdf"
                                />

                                <span
                                    style={{ color: "#0379C9", cursor: "pointer", fontSize: "20px" }}
                                    onClick={() => document.getElementById(`policyFile-${index}`).click()}
                                    title='Allowed file formats: jpg, jpeg, png, pdf, zip'
                                >

                                    <i className="fa fa-file" aria-hidden="true"></i>
                                </span>
                                {proofOfInvestmentData?.section_6a_declarations[index]?.proof_of_investment && (
                                    <span className="text-truncate" style={{ maxWidth: "150px" }}>
                                        {proofOfInvestmentData?.section_6a_declarations[index]?.proof_of_investment?.name}
                                    </span>
                                )}
                            </div>
                            {/* {errors?.billImage && <CustomError text={errors.billImage} />} */}
                        </td>
                        {/* <td>₹0.00</td> */}
                    </tr>
                ))}
                <tr>
                    <td colSpan="4">
                        <hr />
                    </td>
                </tr>
            </React.Fragment >
        )))


    }
    const handleSelectFY = (value) => {
        setSelectedFinancialYear(value)
    }




    return (
        <div className="cardBox  myRequest p-4">
            <div className='d-flex mb-3'>
                <div >
                    <Link to={"/payroll-employees"}>
                        <BackIcon />
                    </Link>
                </div>
                <div>
                    <h5 >
                        {selectedEmployeeData?.name}
                    </h5>
                </div>
                <div className='ms-2 mt-1'>
                    <span className='span-label'>Emp.ID:{selectedEmployeeData?.emp_id}</span>
                </div>
            </div>

            <CustomTabs tabs={tabsRequest} type="investment" ulClass={"requesttab"} />

            <div className="tab-content">
                <div className="tab-pane fade show active" id="overview">
                    <EmpOverview
                        selectedEmployeeData={selectedEmployeeData}
                    />
                </div>
                <div className="tab-pane fade" id="salaryDetails">
                    <SalaryDetails 
                    getAllEmployeeSalary={getAllEmployeeSalary}/>
                </div>
                <div className="tab-pane fade" id="investments">
                    <div className="requestdata">
                        <div className='d-flex justify-content-between'>
                            <div className="d-flex align-items-center mb-4">
                                <ul className="nav nav-tabs leaveTabs ms-3">
                                    <li className="nav-item">
                                        <button
                                            className="nav-link active"
                                            data-bs-toggle="tab"
                                            data-bs-target="#itDeclaration"
                                            type="button"
                                        // onClick={() => handleInvestmentTabChange("itDeclaration")}
                                        >
                                            IT Declaration
                                            {/* <span>4</span> */}
                                        </button>
                                    </li>
                                    <li className="nav-item">
                                        <button
                                            className="nav-link"
                                            data-bs-toggle="tab"
                                            data-bs-target="#proofOfInvestment"
                                            type="button"
                                        // onClick={() => handleInvestmentTabChange("proofOfInvestment")}
                                        >
                                            Proof of Investments
                                        </button>
                                    </li>

                                </ul>
                            </div>
                            <div className='col-3'>
                                <CustomSelect
                                    data={financialYearListData}
                                    placeholder={t("placeholders.financialYear")}
                                    value={selectedFinancialYear}
                                    handleChange={(selectedOption) => handleSelectFY(selectedOption,)}
                                    name="financialYear"
                                />
                            </div>
                        </div>
                        <div className="tab-content">
                            <div className="tab-pane fade show active " id="itDeclaration">

                                {
                                    itDeclarationData ? <div className='ms-3 '>
                                        <div className='d-flex gap-2 justify-content-between'>
                                            <h5 >
                                                IT Declaration
                                                <span
                                                    className={`badge greenBadge ms-2`}
                                                >
                                                    Declared
                                                </span>


                                            </h5>
                                            <div className="btn-group actionDrp">

                                                <CustomButton
                                                    classNames={"btn btn-primary gradientBg mb-2"}
                                                    handleClick={() => handleInvestmentTabChange("itDeclaration")}
                                                    text={"Update"}
                                                    icon={<Icon className="fa fa-edit" />}
                                                />

                                            </div>
                                        </div>
                                        <div className="alert " role="alert" style={{ background: "rgb(247, 248, 255)" }}>
                                            <Icon className={"bi bi-info-circle-fill"}
                                                style={{ color: "rgb(43, 118, 210)" }} />
                                            {" "} Tax Regime : <CustomStrong
                                                text={itDeclarationData?.tax_regime === "is_eligible_for_old" ? "Old Tax Regime" : "New Tax Regime"}
                                            />

                                        </div>
                                        <div className='mt-4'>
                                            <table class="table " style={{ border: "snow", fontSize: "12px" }}>
                                                <thead class="table-light">
                                                    <tr>
                                                        <th>PARTICULARS</th>
                                                        <th>DECLARED AMOUNT</th>
                                                    </tr>
                                                </thead>
                                                <tbody>

                                                    {renderedInvestmentData()}

                                                </tbody>
                                            </table>
                                        </div>
                                    </div> :
                                        <InvestmentNotFound
                                            handleChange={handleInvestmentTabChange}
                                            type={"itDeclaration"}
                                            headingText={"IT Declaration submission is locked for this employee"}
                                            paragraphText={"You can either allow the employee to submit IT Declaration through the portal or submit it on their behalf"}
                                        />}
                            </div>
                            <div className="tab-pane fade" id="proofOfInvestment">
                                {
                                    proofOfInvestmentData ? <div className='ms-3 '>
                                        <div className='d-flex gap-2 justify-content-between'>
                                            <h5 >
                                                Proof Of Investment
                                                <span
                                                    className={`badge blueBadge ms-2`}
                                                >
                                                    Draft
                                                </span>

                                            </h5>
                                            {/* <CustomButton
                                                    classNames={"btn btn-primary gradientBg mb-2"}
                                                    handleClick={() => handleInvestmentTabChange("proofOfInvestment")}
                                                    text={"Update"}
                                                    icon={<Icon className="fa fa-edit" />}
                                                /> */}
                                            {/* <div className="btn-group actionDrp">
                                            
                                            <button type="button" className="btn dropdown-toggle-split"
                                                data-bs-toggle="dropdown" aria-expanded="false">
                                                <i className="bi bi-three-dots-vertical"></i>
                                            </button>
                                            <ul className="dropdown-menu">
                                                <li>
                                                    <span className="dropdown-item"
                                                        onClick={() => handleInvestmentTabChange("proofOfInvestment")}
                                                    >
                                                        <Icon className={"fa fa-edit test"} /> Update</span>
                                                </li>
                                              


                                            </ul>
                                        </div> */}
                                        </div>
                                        <div className="alert " role="alert" style={{ background: "rgb(247, 248, 255)" }}>
                                            <Icon className={"bi bi-info-circle-fill"}
                                                style={{ color: "rgb(43, 118, 210)" }} />
                                            Tax Regime :{"New Tax Regime"}
                                        </div>
                                        <div className='mt-4'>
                                            <table class="table " style={{ border: "snow", fontSize: "12px" }}>
                                                <thead class="table-light">
                                                    <tr>
                                                        <th>PARTICULARS</th>
                                                        <th>DECLARED AMOUNT</th>
                                                        <th>PROOFS</th>
                                                        {/* <th>COMMENTS</th> */}
                                                        {/* <th>ACTUAL AMOUNT</th> */}
                                                    </tr>
                                                </thead>
                                                <tbody>

                                                    {renderedProfOfInvestment()}

                                                </tbody>
                                            </table>

                                        </div>
                                    </div>
                                        : <InvestmentNotFound
                                            handleChange={handleInvestmentTabChange}
                                            type={"proofOfInvestment"}
                                            headingText={"POI submission is locked for this employee"}
                                            paragraphText={"You can either allow the employee to submit POI through the portal or submit the investment proofs on their behalf"}
                                        />}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Investments
