import React, { useRef } from 'react'
import { CustomStrong, Icon, Label, Input, CustomError } from '../../commanComponent'
import CustomTooltip from '../../commanComponent/Tooltip'
import { getInputPrefix } from '../../../utils/common'
import html2pdf from 'html2pdf.js';


const SalaryDetails = ({ getAllEmployeeSalary }) => {
    const pdfRef = useRef();

    const handleDownloadPDF = () => {
        const element = pdfRef.current;
        const opt = {
            margin: 0.20,
            filename: 'Employee_CTC.pdf',
            image: { type: 'jpeg', quality: 0.98 },
            html2canvas: { scale: 2 },
            jsPDF: { unit: 'in', format: 'a4', orientation: 'portrait' }
        };
        html2pdf().set(opt).from(element).save();
    };

    const renderedCTCGenerate = (employeeSalaryDetail) => {
        return (
            <table className=" employee-salary-table w-100" >
                <thead>
                    <tr style={{ borderBottom: "1px solid #dddddd" }}>
                        <th scope="col">Salary Components</th>
                        <th scope="col">Calculation Type</th>
                        <th scope="col">Monthly Amount</th>
                        <th scope="col">Annual Amount</th>
                        <th scope="col"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <th colSpan="5">
                            <h4>Earnings</h4>
                        </th>
                    </tr>

                    {employeeSalaryDetail && employeeSalaryDetail?.earnings &&
                        employeeSalaryDetail?.earnings?.map((item, index) => (
                            <tr key={item.name}>
                                <td>{item.payslip_name}</td>
                                <td>

                                    <div className="input-group mb-3">

                                        <div className="input-group mb-3">
                                            <h6 className='table-text'>
                                                <i aria-hidden="true">{getInputPrefix(item.value_in)}</i>{' '}
                                                {item.value}
                                            </h6>
                                        </div>
                                    </div>
                                </td>
                                <td>
                                    <div className="input-group mb-3">
                                        <h6 className='table-text'>

                                            <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                            {item.monthly}

                                        </h6>
                                    </div>

                                </td>
                                <td>
                                    <div className="input-group mb-3">

                                        <CustomStrong
                                            text={item.yearly}
                                            className='table-text'
                                        ></CustomStrong>
                                    </div>


                                </td>
                            </tr>
                        ))}
                    <tr className='total-rows'>
                        <th colSpan={"2"}>
                            <h6 style={{ color: 'green' }}>
                                <b>GROSS</b>
                            </h6>
                        </th>

                        <th>
                            <h6 style={{ color: 'green' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.gross?.monthly}
                                </b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'green' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.gross?.yearly}
                                </b>
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr>
                        <th colSpan="5">
                            <h4 >Deductions</h4>
                        </th>
                    </tr>
                    <tr>
                        <td colSpan="5">
                            <b style={{ fontSize: "17px" }}>Employee`s Contribution</b>
                        </td>
                    </tr>
                    {employeeSalaryDetail && employeeSalaryDetail?.deductions &&
                        employeeSalaryDetail?.deductions.map((item, index) => (
                            <tr key={index}>
                                {item.name.match(/employee/i) && (
                                    <>
                                        <td>{item.payslip_name}</td>
                                        <td>
                                            <div className="input-group mb-3">

                                                <div className="input-group mb-3">
                                                    <h6 className='table-text'>
                                                        <i aria-hidden="true">{getInputPrefix(item.value_in)}</i>{' '}
                                                        {item.value}
                                                    </h6>
                                                </div>
                                            </div>

                                        </td>
                                        <td>
                                            <div className="input-group mb-3">
                                                <div className="input-group mb-3">
                                                    <h6 className='table-text'>

                                                        <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                                        {item.monthly}

                                                    </h6>
                                                </div>
                                            </div>
                                        </td>
                                        <td>
                                            <div className="input-group mb-3">

                                                <CustomStrong
                                                    text={item.monthly}
                                                    className='table-text'
                                                ></CustomStrong>
                                            </div>

                                        </td>

                                    </>
                                )}
                            </tr>
                        ))}
                    <tr className='total-rows'>
                        <th colSpan="2">
                            <h6 style={{ color: 'black' }}>
                                <b style={{ fontSize: "17px" }}>Total Employee Contribution</b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employee?.monthly}
                                </b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employee?.yearly}
                                </b>
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr >
                        <td colSpan="5" >
                            <b style={{ fontSize: "17px" }}>Employer`s Contribution</b>
                        </td>
                    </tr>
                    {employeeSalaryDetail && employeeSalaryDetail?.deductions &&
                        employeeSalaryDetail.deductions.map((item, index) => (
                            <tr key={index}>
                                {(item.name.match(/employer/i) ||
                                    item.name.match(/gmc/i)) && (
                                        <>
                                            <td>{item.payslip_name}</td>
                                            <td>
                                                <div className="input-group mb-3">
                                                    <div className="input-group mb-3">
                                                        <h6 className='table-text'>

                                                            <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                                            {item.value}

                                                        </h6>
                                                    </div>
                                                </div>
                                            </td>
                                            <td>
                                                <div className="input-group mb-3">

                                                    <div className="input-group mb-3">
                                                        <h6 className='table-text'>

                                                            <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                                            {item.monthly}

                                                        </h6>
                                                    </div>



                                                </div>
                                            </td>
                                            <td>
                                                <div className="input-group mb-3">

                                                    <CustomStrong
                                                        text={item.yearly}
                                                        className='table-text'
                                                    ></CustomStrong>

                                                </div>


                                            </td>
                                        </>
                                    )}
                            </tr>
                        ))}
                    <tr className='total-rows'>
                        <th colSpan="2" >
                            <h6 style={{ color: 'black' }}>
                                <b>Total Employer Contribution</b>
                            </h6>
                        </th>

                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employer?.monthly}
                                </b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employer?.yearly}
                                </b>
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr >
                        <th colSpan="2">
                            <h6 >
                                CTC A
                            </h6>
                        </th>
                        <th>
                            <h6 className='table-text'>

                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.ctc?.monthly}

                            </h6>
                        </th>
                        <th>
                            <h6 className='table-text'>
                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.ctc?.yearly}
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr>
                        <th colSpan="2">
                            <h6 >
                                BONUS
                            </h6>
                        </th>
                        <th>
                            <h6 className='table-text'>
                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.bonus?.monthly}
                            </h6>
                        </th>
                        <th>
                            <h6 className='table-text'>
                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.bonus?.yearly}
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr className='total-rows'>
                        <th colSpan="2">
                            <h5>
                                TOTAL CTC
                            </h5>
                        </th>

                        <th>
                            <h5 className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_ctc?.monthly}
                                </b>
                            </h5>
                        </th>
                        <th>
                            <h5 className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_ctc?.yearly}
                                </b>
                            </h5>
                        </th>
                        <th></th>
                    </tr>
                    <tr className='total-rows'>
                        <th colSpan="2">
                            <h5>
                                TAKE HOME
                            </h5>
                        </th>
                        <th>
                            <h5 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.take_home?.monthly}
                                </b>
                            </h5>
                        </th>
                        <th>
                            <h5 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.take_home?.yearly}
                                </b>
                            </h5>
                        </th>
                        <th></th>
                    </tr>
                </tbody>
            </table>
        )
    }

    return (
        <div>
            <div className="cardHead">
                <h3>
                    Salary Details
                </h3>
            </div>
            {getAllEmployeeSalary && getAllEmployeeSalary.length
                ?
                <div style={{ width: "80%" }}>
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-4 d-flex ">
                                <div className="form-group mb-2">
                                    <Label text={"Annual CTC"} />
                                    <p>
                                        <CustomStrong text={`₹ ${getAllEmployeeSalary[0]?.ctc?.yearly?.toLocaleString("en-IN")} per year`}
                                            style={{ fontSize: "18px" }}
                                        />
                                    </p>
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-2">

                                    <div className="form-group mb-2">
                                        <Label text={"Monthly CTC"} />
                                        <p>
                                            <CustomStrong text={`₹ ${getAllEmployeeSalary[0]?.ctc?.monthly?.toLocaleString("en-IN")} per month`}
                                                style={{ fontSize: "18px" }}
                                            />
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4" style={{ textAlign: "right", marginTop: "30px" }}>
                                <div className="form-group mb-2">

                                    <div className="form-group mb-2">
                                        <span className='salary-print' onClick={() => handleDownloadPDF()}>
                                            <CustomTooltip text={"Print Salary Certification"} element={<i class="bi bi-printer"></i>} />
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                        <div className='cardBox' ref={pdfRef}>
                            {renderedCTCGenerate(getAllEmployeeSalary[0])}
                        </div>
                    
                </div>
                : <div className='d-flex justify-content-center'>
                    <p>
                        <CustomError text={`Salary Not Assign`}
                            style={{ fontSize: "18px" }}
                        />
                    </p></div>}
        </div>
    )
}

export default SalaryDetails
