import React from 'react'
import { Icon } from '../../commanComponent'
import CustomTooltip from '../../commanComponent/Tooltip'
import { dateFormate } from '../../../utils/common'

const EmpOverview = (props) => {
    const {selectedEmployeeData}=props
    return (
        <div className="container">
            <div className="main-body">
                <div className="row gutters-sm">
                    <div className="col-md-5 mb-3">
                        <div className="cardBox ">
                            <div className="card-body">
                                <div className="d-flex flex-column align-items-center text-center">

                                    <img src="images/profile.png" alt="Admin" class="rounded-circle" width="50" />
                                    <div className="mt-3">
                                        <h4>{selectedEmployeeData?.name}</h4>
                                        <p className="text-secondary mb-1">Full Stack Developer</p>
                                        <p className="text-muted font-size-sm">
                                        {selectedEmployeeData?.address}
                                        </p>
                                    </div>
                                </div>
                                <hr />
                                <div>
                                    <div className='basic-info-title'>Basic Information</div>
                                    <ul className=" listing-groups">
                                        <li className="d-flex flex-wrap">
                                            <CustomTooltip text={"Email ID"} element={<i className={"bi bi-envelope"} />} />
                                            <span>{selectedEmployeeData?.email || '-'}</span>
                                        </li>
                                        <li className="d-flex flex-wrap">
                                            <CustomTooltip text={"Mobile No."} element={<i className={"bi bi-phone"} />} />
                                            <span>{selectedEmployeeData?.mobile || '-'}</span>
                                        </li>
                                        <li className="d-flex flex-wrap">
                                            <CustomTooltip text={"Gender"} element={<i className={"bi bi-person"} />} />

                                            <span>{selectedEmployeeData?.gender || '-'}</span>
                                        </li>
                                        <li className="d-flex flex-wrap">
                                            <CustomTooltip text={"Date of Joining"} element={<i className={"bi bi-calendar2"} />} />
                                           {selectedEmployeeData?.joining_date ?<> <span>{dateFormate(selectedEmployeeData?.joining_date) }</span>
                                            <span className='text-secondary ms-1' style={{ fontSize: "14px" }}>(Date of Joining)</span>
                                           
                                            </>: '-'}
                                        </li>
                                        <li className="d-flex flex-wrap">
                                            <CustomTooltip text={"Office Location"} element={<i className="bi bi-geo-alt" />} />
                                            <span>{selectedEmployeeData?.location_name || '-'}</span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-7">
                        <div className="cardBox mb-3">
                            <div className="card-title">
                                <h5>
                                    Personal Information
                                </h5>
                            </div>
                            <div className="card-body">
                                <div className="row mb-3">
                                    <div className="col-sm-4">
                                        <h6 className="mb-0 text-secondary">Date of Birth</h6>
                                    </div>
                                    <div className="col-sm-8 ">{dateFormate(selectedEmployeeData?.dob) || '-'}</div>
                                </div>

                                <div className="row mb-3">
                                    <div className="col-sm-4">
                                        <h6 className="mb-0 text-secondary">Father's Name</h6>
                                    </div>
                                    <div className="col-sm-8 ">{selectedEmployeeData?.father_name || '-'}</div>
                                </div>
                                <div className="row mb-3">
                                    <div className="col-sm-4">
                                        <h6 className="mb-0 text-secondary">PAN No</h6>
                                    </div>
                                    <div className="col-sm-8">WEWEW3232E</div>
                                </div>
                                <div className="row mb-3">
                                    <div className="col-sm-4">
                                        <h6 className="mb-0 text-secondary">Personal Email Address</h6>
                                    </div>
                                    <div className="col-sm-8 ">sarfarazc15@gamil.com</div>
                                </div>

                                <div className="row mb-3">
                                    <div className="col-sm-4">
                                        <h6 className="mb-0 text-secondary">Residential Address</h6>
                                    </div>
                                    <div className="col-sm-8 ">-</div>
                                </div>
                                <div className="row mb-3">
                                    <div className="col-sm-4">
                                        <h6 className="mb-0 text-secondary">Differently Abled Type</h6>
                                    </div>
                                    <div className="col-sm-8 ">Visual</div>
                                </div>

                            </div>
                        </div>
                        <div className="cardBox mb-3">
                            <div className="card-title">
                                <h5>
                                    Payment Information
                                </h5>
                            </div>
                            <div className="card-body">
                                <div className="row mb-3">
                                    <div className="col-sm-3">
                                        <h6 className="mb-0 text-secondary">Payment Mode</h6>
                                    </div>
                                    <div className="col-sm-9 ">Cheque</div>
                                </div>

                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>

    )
}

export default EmpOverview
